import React, { useState, useRef } from 'react';
import cedarsMark from "../../../assets/images/cedars-mark.png"
import { ReactComponent as RadialImage } from '../../../assets/images/radial-image.svg';
import BottomToTopTransition from '../../../components/transitionContainer';
import success from '../../../assets/images/success.png';
import team from '../../../assets/images/team.png';
import approach from '../../../assets/images/approach.png';
import '../css/header.css'

import { Button } from 'primereact/button';
import { Dialog } from 'primereact/dialog';
import { InputText } from 'primereact/inputtext';
import { InputTextarea } from 'primereact/inputtextarea';
import { Toast } from 'primereact/toast';

import 'react-phone-number-input/style.css'
import PhoneInput from 'react-phone-number-input'

import emailjs from 'emailjs-com';

const Header = () => {
  const strategyItems = [
    { id: 1, image: approach, text: 'Strategic Approach' },
    { id: 2, image: team, text: 'Professional Team' },
    { id: 3, image: success, text: 'Guaranteed Success' },
  ];

  const [isDialogVisible, setDialogVisible] = useState(false);
  const toast = useRef(null);

  const show = () => {
      toast.current.show({ severity: 'success', summary: 'success', detail: 'Message Content' });
  };

  const [formData, setFormData] = useState({
    name: '',
    email: '',
    message: '',
    phone : ''
  });

  const [statusMessage, setStatusMessage] = useState('');

  const handleInputChange = (value, name) => {
    setFormData((prevData) => ({
      ...prevData,
      [name]: value,
    }));
  };

  // Send email function
  const sendEmail = (e) => {
    e.preventDefault();

    // EmailJS configuration
    const templateParams = {
      name: formData.name,
      email: formData.email,
      message: formData.message,
    };
    
    emailjs
      .sendForm('service_88cyfmk', 'template_tr8r42q', e.target, 'ihqM9397oaK-Edcd1')
      .then(
        (result) => {
          console.log(result.text);
          setStatusMessage('Email sent successfully!');
          setDialogVisible(false)
          toast.current.show({ severity: 'success', summary: 'success', detail: 'Email sent successfully!' });
        },
        (error) => {
          console.log(error.text);
          setStatusMessage('Failed to send email. Please try again.');
        }
      );
  };

  return (
    <div>
        <div class="image-container">
            <img src={cedarsMark} class="fixed-image" alt="Fixed Position Image" />
        </div>

        <div className='svg-radial-container'>
         <RadialImage />
        </div>

        <BottomToTopTransition>

         <header className="header">
            <div className='header-heading'>Crafting Digital Innovation Products</div>
            <div className='gradient-text'>For Customer-Centric Businesses</div>
            <h3 className='subheading'>Your partner in building digital products that scale</h3>

            <button 
              className='button-style get-quote-btn-banner' 
              style={{"borderRadius": "80px", "backgroundColor": "#71FA71"}}
              onClick={() => setDialogVisible(true)}
              >
                Get a Quote
            </button>

            <div className="info">
            </div>  
        </header>
        
        <div className="strategy-component-container">
          {strategyItems.map((item) => (
            <div className="strategy-item" key={item.id}>
              <img src={item.image} alt={item.text} />
              <div className="strategy-item-text">{item.text}</div>
            </div>
          ))}
        </div>
            
        </BottomToTopTransition>

        {/* Get Quote Modal */}
        <Dialog header="Header" className='get-quote-modal' visible={isDialogVisible} draggable={false} onHide={() => {if (!isDialogVisible) return; setDialogVisible(false); }}>
            <div className='get-quote-form'>
                <h2>Let's Get in Touch</h2>
                  <form onSubmit={sendEmail}>
                    <div className="get-quote-form-row">
                      <div className="form-group">
                        <InputText
                          type="text"
                          name="name"
                          required
                          placeholder="Name"
                          value={formData.name}
                          onChange={(e) => handleInputChange(e.target.value, 'name')}
                        />
                      </div>

                      <div className="form-group">
                        <InputText
                          type="email"
                          name="email"
                          required
                          placeholder="Email"
                          value={formData.email}
                          onChange={(e) => handleInputChange(e.target.value, 'email')}
                        />
                      </div>

                      <div className="form-group">
                        <PhoneInput
                          name="phone"
                          placeholder="Enter phone number"
                          required
                          value={formData.phone}
                          onChange={(value) => handleInputChange(value, 'phone')}
                        />
                      </div>

                      <div className="form-group">
                        <InputTextarea
                          name="message"
                          rows={5}
                          cols={30}
                          placeholder="Message"
                          required
                          value={formData.message}
                          onChange={(e) => handleInputChange(e.target.value, 'message')}
                        />
                      </div>

                      <div className="form-group flex justify-content-center">
                        <Button label="Send" className="send-button" />
                      </div>
                    </div>
                </form>
              
                {statusMessage && <p>{statusMessage}</p>}
            </div>
        </Dialog>

        <Toast ref={toast} />
    </div>
  );
};

export default Header;
